import { UpdateType } from "../model/model";
import { CardPayload, cardFieldToEmptyPayload } from "../model/model-payload";
import { CardField, emptyContent } from "../model/model-content";
import { CreateModify, createReplace, changeCardObject } from "../model/model-permutation";

const createAppendStr: CreateModify = (inputValue: string) => (oldValue: string) => oldValue + inputValue;

export const contentStringToConfig = (
	contentStr: string,
	outputField: CardField,
	updateType: UpdateType,
	existingCard?: CardPayload
): CardPayload => {
	const objectToBeChanged = existingCard ? existingCard.content : emptyContent(outputField);
	const changeSpec = {
		inputObject: contentStr,
		inputField: "note" as CardField,
		fieldToBeChanged: outputField,
		objectToBeChanged,
		createModify: updateType === "APPEND" ? createAppendStr : createReplace,
	};
	const content = changeCardObject(changeSpec);

	return existingCard ? { ...existingCard, content } : { ...cardFieldToEmptyPayload(outputField), content };
};
